import { Dispatcher, IStoreWithData, storeUtils, toProxy } from "@fanfanlo"
import { IVisit, tmsVisitAppStart } from "../remote/visit"

const ns = 'user'

interface IUserStoreData{
    user_token?:string
}
interface IUserSessionData{
    visit?:IVisit
}
export interface IUserDataShell {
    storeData:IUserStoreData
    sessionData:IUserSessionData
}
function createDefaultStoreData(){
    return {
        user_token:undefined
    }
}
function createDefaultUserData():IUserDataShell{
    return {
        storeData:createDefaultStoreData(),
        sessionData:{
            visit:undefined
        }
    }
}

let store:IStoreWithData<IUserStoreData>

class User extends Dispatcher{
    data = toProxy(createDefaultUserData())
    constructor(){
        super()
    }
    async init(){
        store = await storeUtils.getNamespaceStoreWithData(ns, {forceReset:false, mergeDefData:true, defData:createDefaultStoreData()});
        this.data.storeData = store.data;
        console.log('user init',this.data.storeData);
        const res = await tmsVisitAppStart({params:{device_id:"androidgetid"}, handler:{printLog:false}})
        if(res.data){
            this.data.sessionData.visit = res.data;
        }
        console.log('user init',this.data.sessionData.visit)
    }
    async signout(){
        this.data.storeData.user_token = undefined;
    }
    async setUserToken(token:string){
        this.data.storeData.user_token = token;
    }

}
export const user = new User();